import React from "react";
import datafiles from "./datafiles";
import Card from "./Card";
const Category=()=>{

    function mycard(val){
        return (
            <Card 
            name={val.name}
            offer={val.offer}
            imgUrl={val.imgUrl}
            description={val.description}
            price={val.price}
            order={val.order}
            />
        );
    }
return (
    <div>
    {datafiles.map((mycard))}

   
       
     </div>
);

}
export default Category;